import { Component } from '@angular/core';
import { LoadingController,ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-categoriaformsusr-create',
  templateUrl: 'categoriaformsusr-create.html',
})
export class CategoriaformsusrCreatePage {

 // @ViewChild('portComponent') portComponent: IonicSelectableComponent;
 ports: any[];
 port: any;

 public form                  : FormGroup;
 username: string = "";
 public idformulario     : any;     
 idusuario2:any;       
 idusuario2n:any;       
 public idusuario		     : any;
 idempresa:any;
 public vidformulario  :any=[];
 public vidusuario  :any=[];
idcategoria:any;
 private _ID                  : String;
 public pageTitle           	: string;

 constructor( public storage: Storage, private loadingCtrl: LoadingController,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
   public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
         public events: Events)
 {
    // Define form validation - VERY basic!
    this.form = this._FB.group({
      idformulario     : ['', Validators.required],
      idusuario    :['', Validators.required]
   
    });

    
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
      
      
  })
 
 }

 ngOnInit() {
   this.storage.get('username').then((value) => {
     this.username = value;
 }); 
 this.focused('a')
 }

 ionViewDidLoad() : void
 {
  this.idcategoria=this.navParams.data.record2.idcategoria;
 
  this.authService.getReg(this.idcategoria + '/categoria/' + this.idempresa,'frmmovils').then((result) => {
    this.vidformulario=result;
  }, (err) => {  
  this.msj.showError3('Error',err.error);
});
/*
this.authService.getReg('','personals').then((result) => {
  this.vidusuario=result;
 
}, (err) => {  
this.msj.showError3('Error',err.error);
});
*/


   if(this.navParams.get("record")==null)
   {
     this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
       this.pageTitle=value;
     });
   }
   else {
      this._ID 				=	this.navParams.data.record._id;
      this.idformulario		=	this.navParams.data.record.idformulario;
      this.idusuario		=	this.navParams.data.record.idusuario.cui;
      this.focused('a');
      this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
       this.pageTitle=value;
     });
   }
 }

 

 manageGallery() : void
 {
 let creat='Actualiza';
   if(this.navParams.get("record")==null)
   {  creat='Crea';
   }
    let   options       : any	     = { 
      idempresa:this.idempresa,
    idformulario : this.form.controls['idformulario'].value,
    idcategoria: this.idcategoria,
    idusuario: this.idusuario2,
    nombre: this.idformulario.nombre + ' ' + this.idusuario2n,
    bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' Categorias de usuarios, formularios '}
   };
     

    if(this.navParams.get("record")==null)
    {  
         if(options){
           this.authService.createReg('',options,'catusuarios').then((result) => {
             if (!this.form.valid) { return; }
             this.viewCtrl.dismiss(result);
         }, (err) => {  
           this.msj.showError3('Error',err.error);
         });
         }   
    }   
    else
    {
     if(options){
       this.authService.createReg(this._ID,options,'catusuarios').then((result) => {
          if (!this.form.valid) { return; }
       //   console.log(result)
          this.viewCtrl.dismiss(result);
      }, (err) => {
       this.msj.showError3('Error',err.error);
      });
   }}
 }

 cancel() {
   this.viewCtrl.dismiss();
 }
 done() {
   if (!this.form.valid) { return; }
   //this.viewCtrl.dismiss(this.form.value);
 }



items: any=[];

focused(selectedValue: any) {
  this.idusuario2				=	''
  if(( this.idusuario) && (this.idusuario!=''))
  {

   const loading = this.loadingCtrl.create({
     spinner: 'bubbles',
     content: "Espere un momento,  Buscando informaciÃ³n ......"
   });
   loading.present();


   this.items=[]
   this.authService.getReg(this.idusuario+'/'+'persona/' + this.idempresa + '/' + this.idempresa,'personals').then((data) => {
   
    if(data)
    {
     this.idusuario2				=	data[0]._id;
     this.idusuario2n				=	data[0].nombre;
     
     this.items=data;
     loading.dismiss();
    }  
   
 }, (err) => {


this.msj.showError3('Resultado','No idusuario invalido ');
this.idusuario=''
this.items=[]
loading.dismiss();


  
  
 });

  }
   
 }


}



